/** @format */

/**
 * Set up a decent box model on the root element
 */

@import url("https://fonts.googleapis.com/css?family=Lato:300,400&display=swap");

html {
  box-sizing: border-box;
  scroll-behavior: smooth;
  background: transparent;
  margin: 0;
  padding: 0;
}

/**
 * Make all elements from the DOM inherit from the parent box-sizing
 * Since `*` has a specificity of 0, it does not override the `html` value
 * making all elements inheriting from the root box-sizing value
 * See: https://css-tricks.com/inheriting-box-sizing-probably-slightly-better-best-practice/
 */

*,
*::before,
*::after {
  box-sizing: inherit;
}

/**
 * Basic styles for links
 */

a {
  color: #e50050;
  text-decoration: none;
}

a:hover {
  color: #222222;
  text-decoration: none;
}

/*
* These styles creates the underlining of link with class 'special link'
*/

.special-link {
  position: relative;
  text-decoration: none;
  transition: color ease 0.3s;
}

.special-link::after {
  content: '';
  position: absolute;
  z-index: -1;
  width: 100%;
  height: 10%;
  left: 0;
  bottom: 0;
  background-color: #da972c;
  transition: all ease 0.3s;
}

.special-link:hover::after {
  height: 100%;
}

/** @format */

/**
 * Basic typography style for copy text
 *
 * @format
 */

body {
  color: #222222;
  font: normal 125%/1.4 "Lato", "Open Sans", "Helvetica Neue Light", "Helvetica Neue", "Helvetica", "Arial", sans-serif;
}

/**
 * Clear inner floats
 */

.clearfix::after {
  clear: both;
  content: '';
  display: table;
}

/**
* Sticky footer at the bottom using CSS Grid
* header > content > footer
*/

.wrapper {
  width: 100%;
  min-height: 100vh;
  display: grid;
  grid-template-rows: auto 1fr auto;
  grid-template-columns: 1fr;
}

